import clsx from "clsx";
import { FC, PropsWithChildren } from "react";
import ColBox from "../components/ColBox";

export type InvitationProps = {};

export enum TypoLinePosition {
  center,
  left,
  right,
}

export type TypoLineProps = {
  className?: string;
  position?: TypoLinePosition;
};

const Space = (count = 1) => {
  return Array.from({ length: count })
    .map((_, idx) => "\u00A0")
    .join("");
};

const TypoLine: FC<PropsWithChildren<TypoLineProps>> = ({
  children,
  position = TypoLinePosition.center,
  className = "",
}) => (
  <p
    className={clsx(
      {
        ["text-center"]: position === TypoLinePosition.center,
        ["text-left"]: position === TypoLinePosition.left,
        ["text-right"]: position === TypoLinePosition.right,
      },
      className
    )}
  >
    {children}
  </p>
);

const StrogText: FC<PropsWithChildren> = ({ children }) => (
  <strong className="text-primary text-2xl">{children}</strong>
);

const Invitation: FC = () => {
  return (
    <ColBox>
      <div>
        <TypoLine className="text-xl">
            <StrogText></StrogText>  
        </TypoLine>
        <TypoLine className="text-xl">
            <StrogText></StrogText> 
        </TypoLine>
        <TypoLine className="text-xl">
            <StrogText></StrogText> 
        </TypoLine>
        <TypoLine className="text-xl">
           <StrogText></StrogText> .
        </TypoLine>
        <br />
        <br />
        <TypoLine className="text-sm xs:text-lg">
              
        </TypoLine>
        <TypoLine className="text-sm xs:text-lg">
              .
        </TypoLine>
      </div>
      <div>
        <TypoLine className="text-2xl">
          {Space(6)}{Space(5)}  {Space(1)}
          <StrogText>{Space(2)}</StrogText>
        </TypoLine>
        <TypoLine className="text-2xl">
              {Space(1)}
          <StrogText>{Space(2)}</StrogText>
        </TypoLine>
      </div>
      <div>
        <h2 className="text-primary text-center xs:text-2xl">
          {Space(2)}   .{Space(2)}
        </h2>
        <h2 className="text-primary text-center xs:text-2xl">
            .
        </h2>
      </div>
    </ColBox>
  );
};

export default Invitation;
